  color: var(--color-default-white);
  font-weight: 700;
  text-align: center;
  line-height: 1;
  cursor: pointer;
  transition: var(--default-transition);
}
@media (min-width: 768px) {
  .button {
    padding: 2.4rem 1.6rem;
  }
}
.button:hover {
  opacity: 0.7;
}
.button--white {
  background-color: var(--color-default-white);
  color: var(--color-default-black);
}
.button--border-bottom {
  border-bottom: 0.1rem solid var(--color-default-black);
}
.button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.title {
  font-size: 2.6rem;
  font-weight: 700;
  line-height: 1;
}
@media (min-width: 768px) {
  .title {
    font-size: 2.8rem;
  }
}
@media (min-width: 1200px) {
  .title {
    font-size: 3.2rem;
  }
}

.link {
  position: relative;
  font-weight: 500;
}
@media (min-width: 768px) {
  .link {
    font-size: 1.8rem;
  }
}
.link::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.2rem;
  bottom: -0.5rem;
  left: 0;
  background-color: var(--color-default-black);
}

.link-container {
  display: flex;
  justify-content: center;
  margin-top: 4rem;
}

.title-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .title-container {
    margin-bottom: 6rem;
  }
}
@media (min-width: 1200px) {
  .title-container {
    margin-bottom: 7rem;
  }
}

.search-block {
  position: relative;
}
.search-block.active .search-block_input {
  visibility: visible;
  opacity: 1;
}
.search-block .search-block_input {
  width: 23.5rem;
}
@media (min-width: 768px) {
  .search-block .search-block_input {
    width: 40rem;
  }
}

.search-block_input {
  position: absolute;
  right: -1rem;
  top: -0.9rem;
  padding: 0.5rem 3rem 0.5rem 1rem;
  visibility: hidden;
  opacity: 0;
}
@media (min-width: 768px) {
  .search-block_input {
    height: 5rem;
    width: 40rem;
    top: -1.1rem;
  }
}

.search-block_button {
  position: absolute;
  right: -0.5rem;
  padding: 0.5rem;
  background-color: transparent;
}

.search-block_img {
  min-width: 1.8rem;
}

.burger {
  position: relative;
  width: 2.4rem;
  background-color: unset;
}

.burger_open,
.burger_close {
  position: absolute;
  left: 0;
}

.burger_close:after {
  opacity: 0;
}

.burger_open:after,
.burger_close:after,
.burger_close:before {
  content: "";
  position: absolute;
  background: var(--color-default-black);
  width: 22px;
  height: 2px;
  transition: opacity 0.2s;
}

.burger_open:after {
  box-shadow: 0 -7px var(--color-default-black), 0 7px var(--color-default-black);
}

.active .burger_open:after {
  opacity: 0;
  transition: opacity 0.2s;
}

.active .burger_close:after {
  opacity: 1;
  transition: opacity 0.2s;
}

.active .burger_close:after {
  transform: rotate(-45deg);
}

.active .burger_close:before {
  transform: rotate(45deg);
}

.bread-crumbs {
  display: flex;
  flex-wrap: wrap;
}

.bread-crumbs_link {
  position: relative;
  font-size: 1.8rem;
  line-height: 1;
  opacity: 1;
}
.bread-crumbs_link:not(:first-child) {
  margin-left: 3rem;
}
.bread-crumbs_link:not(:first-child)::before {
  content: "/";
  position: absolute;
  left: -1.5rem;
  opacity: 1;
}
.bread-crumbs_link:last-child {
  opacity: 0.4;
}

.accordion_item {
  border-top: 1px solid var(--color-light-grey);
  border-bottom: 1px solid var(--color-light-grey);
}
.accordion_item.active .accordion_icon {
  transform: scaleY(-1);
}
.accordion_item.active .accordion_wrapper {
  max-height: 100%;
}

.accordion_title-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.8rem 1.5rem;
  cursor: pointer;
}

.accordion_title {
  margin: 0;
  user-select: none;
  font-weight: 500;
}

.accordion_icon {
  transition: var(--default-transition);
  width: 1.6rem;
  height: 0.8rem;
}

.accordion_wrapper {
  max-height: 0;
  overflow: hidden;
  transition: var(--default-transition);
}

.accordion_content {
  padding: 0.7rem 1.5rem 2.5rem 1.5rem;
}

.sort-buttons {
  display: flex;
  flex-wrap: wrap;
  margin-left: -0.5rem;
  gap: 1.5rem 2.5rem;
}

.sort-buttons_button {
  padding: 0.5rem;
  font-size: 1.4rem;
  color: var(--color-grey);
  font-weight: 500;
  transition: var(--default-transition);
}
.sort-buttons_button:hover {
  opacity: 0.7;
}
.sort-buttons_button.active {
  color: var(--color-default-black);
  text-shadow: 0px 0px 0.1rem var(--color-default-black);
}

.filter-radio {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem 3rem;
}

.filter-radio_item {
  width: 2.2rem;
  height: 2.2rem;
}

.filter-radio_button {
  display: none;
}

.filter-radio_button + label {
  position: relative;
  cursor: pointer;
}

.filter-radio_button + label::before,
.filter-radio_button:checked + label::after {
  content: "";
  position: absolute;
  top: 1.2rem;
  bottom: 0;
  margin: auto;
  border-radius: 50%;
}

.filter-radio_button + label::before {
  left: 0.3rem;
  width: 1.6rem;
  height: 1.6rem;
  background-color: var(--radio-color);
  cursor: pointer;
}

.filter-radio_button:checked + label::after {
  left: 0;
  width: 2.2rem;
  height: 2.2rem;
  border: 0.1rem solid var(--radio-color);
  cursor: pointer;
}

.input-field {
  padding: 1rem;
  width: 100%;
  outline: none;
  font-size: 1.8rem;
  height: 4.7rem;
  border: 0.2rem solid var(--color-default-black);
  transition: var(--default-transition);
  font-weight: 500;
}

.close-button {
  padding: 0;
  position: relative;
  width: 3rem;
  height: 3rem;
  font-size: 0;
}
.close-button::before, .close-button::after {
  content: "";
  position: absolute;
  width: 2rem;
  height: 0.2rem;
  background-color: var(--color-default-black);
  left: 0.5rem;
  top: 1.5rem;
  transform: rotate(135deg);
}
.close-button::after {
  transform: rotate(45deg);
}
.close-button:hover {
  opacity: 0.7;
  transition: var(--default-transition);
}